.search-button {
	display: inline-flex;
	align-items: center;
	justify-content: center;
	background-color: #007cba;
	color: white;
	border-radius: 50%;
	padding: 12px;
	text-decoration: none;
	transition: background-color 0.2s ease;
}

.search-button:hover {
	background-color: #005f8d;
}